* {
  margin: 0;
  padding: 0;
  font-family:sans-serif;
} 


body {
  background-image: url('../src/night.jpg');
  background-size: cover;
  
}
 #up-showcase{
padding:200px;
}
#showcase{
color: whitesmoke;
width:55%;
margin: auto;
border-radius: 10px;
display: flex;
justify-content: center;
padding: 10px 10px;
border: 1px solid #7d7777;
background-color: #191919;


}
#icons{
display: flex;
justify-content: center;
gap: 15px;
}
#jobtitle h1{
display: inline-block;
font-weight: 400;
border-bottom: 2px solid #696262;
}
.button-27 {
appearance: none;
background-color: #000000;
border: 2px solid #1A1A1A;
border-radius: 15px;
box-sizing: border-box;
color: #FFFFFF;
cursor: pointer;
display: inline-block;
font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
font-size: 16px;
font-weight: 600;
line-height: normal;
margin: 0;
min-height: 60px;
min-width: 0;
outline: none;
padding: 16px 24px;
text-align: center;
text-decoration: none;
transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
user-select: none;
-webkit-user-select: none;
touch-action: manipulation;
width: 100%;
will-change: transform;
}

.button-27:disabled {
pointer-events: none;
}

.button-27:hover {
box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
transform: translateY(-2px);
}

.button-27:active {
box-shadow: none;
transform: translateY(0);
}
@media only screen and (max-width: 1024px){
#up-showcase {
  width:95%;
  margin-top: 300px;
}
#showcase {
  width:95%;
  margin:auto;
}
}